
import LoglistCell from '../components/LoglistCell.vue'
import { AjaxHttp, AjaxSilentHttp } from '../../../js/ajaxHttp'
import event from '../../../js/Event.js'
import { mapState } from 'vuex'
import { getYMDFormateTime, isArrayType, getImageUrl, openFile } from "../../../js/utility.js"
import Popup from 'vux/src/components/popup/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import FiltrateView from '../../crm/components/FiltrateView'

export default {
name: 'loglist',
components: {
LoglistCell,
Popup,
Cell,
Actionsheet,
FiltrateView
},
data() {
return {
p: 1,
listItem: [],
listFile: [],
showFileList: false,
showRightMenus: false,
rightMenus: {
day: '日报',
week: '周报',
month: '月报',
},

leftData: [{ name: '创建时间正序', params: { order_type: 'asc', order_field: 'create_date' } },
{ name: '创建时间倒序', params: { order_type: 'desc', order_field: 'create_date' } },
{ name: '修改时间正序', params: { order_type: 'asc', order_field: 'update_date' } },
{ name: '修改时间倒序', params: { order_type: 'desc', order_field: 'update_date' } }
],

filtrateData: [], //筛选数据
navTabIndex: -1, //0 左边的信息 1 右边的信息
rightShow: false, //右侧布局是否展示
leftIndex: -1, //左侧列表的选择 默认不选择 -1
filtrateScrollTop: 0, //滚动距离指右边的布局

filtratePostParams: {}, //筛选参数
}
},
computed: {
...mapState({
logListInfo: state => state.logListInfo,
}),
},
mounted() {
this.initInfo();
},
beforeDestroy() {
event.$off("gotoAddData");
},
beforeRouteLeave(to, from, next) {

if (to.name === 'office') { //离开页面返回上层，需要重置数据
this.$store.commit('updateLogListInfo', {
listItem: [],
listFile: [],
showFileList: false,
p: 1,
scroll: 0,
loadingState: 0,
refresh: false,
});
} else { //记录所有状态
if (to.name === 'selectlist') { //跳转选择列表(负责人)
event.$on('selectedRelateItem', function(data) {
this.handleSelectedInfo(data);
}.bind(this));
}
this.$store.commit('updateLogListInfo', {
listItem: this.listItem, //平台类型与type相同 进行赋值
listFile: this.listFile,
p: this.p,
scroll: this.$refs.my_scroller.getPosition().top,
showFileList: this.showFileList,
loadingState: this.$refs.my_scroller.loadingState,
refresh: false,


filtrateData: this.filtrateData,
navTabIndex: this.navTabIndex,
rightShow: this.rightShow,
leftIndex: this.leftIndex,
filtrateScrollTop: this.filtrateScrollTop,

filtratePostParams: this.filtratePostParams,

});

}
next();
},
methods: {
initInfo() {
event.$on('gotoAddData', function() {
this.showRightMenus = true;
}.bind(this));
this.$store.commit('updateXheaderShowAdd', true);
if (this.logListInfo.listItem.length > 0 && !this.logListInfo.refresh) {
this.listItem = this.logListInfo.listItem;
this.listFile = this.logListInfo.listFile;
this.showFileList = this.logListInfo.showFileList;
this.p = this.logListInfo.p;

this.filtrateData = this.logListInfo.filtrateData;
this.$nextTick(() => {
this.navTabIndex = this.logListInfo.navTabIndex;
this.rightShow = this.logListInfo.rightShow;
this.leftIndex = this.logListInfo.leftIndex;
this.filtrateScrollTop = this.logListInfo.filtrateScrollTop;
this.filtratePostParams = this.logListInfo.filtratePostParams;

this.$refs.my_scroller.resize();
this.$refs.my_scroller.scrollTo(0, this.logListInfo.scroll, false);
if (this.logListInfo.loadingState === 2) {
this.$refs.my_scroller.finishInfinite(true);
}
});
}
},
clickRightMenu(key) {
var type = '';
if (key == 'day') {
type = '4';
} else if (key == 'week') {
type = '3';
} else if (key == 'month') {
type = '2';
}
this.$router.push({
name: 'createlog',
params: {
type: type,
action: 'add'
}
});
},
infinite(done) {
this.getList(done);
},
refresh(done) {
this.p = 1;
this.getList(done);
},
getList(done) {
var postParams = this.filtratePostParams;
postParams.p = this.p,
AjaxSilentHttp('POST', 'm=log&a=index', postParams, this, function(res, wself, success) {
if (success && isArrayType(res.data)) {
var newItems = [];
for (var i = 0; i < res.data.length; i++) {
newItems[i] = wself.getNewItem(res.data[i]);
}
if (wself.p === 1) {

if (isArrayType(res.fields_list) && wself.filtrateData.length === 0) {
wself.handleFiltrateInfo(res.fields_list);
}
wself.listItem = newItems;
} else {
wself.listItem = wself.listItem.concat(newItems);
}

if (typeof done === 'function') {
if (wself.p === 1 && res.data.length < 10) {
done(true);
} else if (res.data.length === 0) {
done(true);
} else {
done();
}
}
wself.p = wself.p + 1;
}
});
},
handleFiltrateInfo(list) {
var tempArray = [];
for (var i = 0; i < list.length; i++) {
var item = list[i];

if (item.form_type === 'address') {
item['fieldvalue'] = [];
item['showAddress'] = false;
} else if (item.form_type === 'datetime') { //添加的时候 因为合同选择不包含具体时间所以加入 form_addition 属性 辅助控制
item['fieldvalue'] = '';
item['fieldvalue2'] = '';
} else {
item['fieldvalue'] = '';
}
tempArray.push(item);
}
this.filtrateData = tempArray;

},
getNewItem(item) {
return { user_name: item.role_info.full_name, create_date: getYMDFormateTime(item.create_date), content: item.content, img: getImageUrl(item.role_info.thumb_path), comment_count: item.comment_count === '0' ? '回复' : item.comment_count, praise_count: item.praise_count === '0' ? '赞' : item.praise_count, is_praised: item.is_praised, file_list: this.getFileList(item.file_list), show_file: this.getMarkImage(item.file_list), id: item.log_id }
},
getFileList(lists) {
let ret = [];
for (var i = 0; i < lists.length; i++) {
var item = lists[i];
item.file_path = getImageUrl(item.file_path);
item.file_path_thumb = getImageUrl(item.file_path_thumb);
ret[i] = item;
}
return ret;
},
getMarkImage(lists) {
if (lists.length === 0) {
return {
img: '',
title: '',
detail: '',
show: false
}
} else if (lists.length === 1) {
var item = lists[0];
return {
img: this.fileTypeImage(item),
title: item.name,
detail: item.size,
show: true
}
} else if (lists.length > 1) {
return {
img: require('../../../assets/file/file_file.png'),
title: '附件：' + lists.length + '个',
detail: '',
show: true
}
}
},
replyLog(item) {
console.log(item.user_name + item.id);
this.$router.push({
name: 'logdetail',
params: {
id: item.id
}
});
},
clickLog(item) {
console.log(item.user_name + item.id);
this.$router.push({
name: 'logdetail',
params: {
id: item.id
}
});
},
previewLog(item) {
if (item.file_list.length > 1) {
this.listFile = item.file_list;
this.showFileList = true;
} else {
this.filepreview(item.file_list[0].file_path);
}
},
fileTypeImage(item) {
var type = item.file_type;
if (type === 'doc' || type === 'DOC' || type === 'docx') {
return require("../../../assets/file/doc.png");
} else if (type === 'xls' || type === 'xlsx') {
return require("../../../assets/file/excel.png");
} else if (type === 'png' || type === 'gif' || type === 'jpg' || type === 'jpeg') {
return item.file_path;
} else if (type === 'pdf' || type === 'PDF') {
return require("../../../assets/file/pdf.png");
} else if (type === 'ppt' || type === 'PPT' || type === 'pptx') {
return require("../../../assets/file/ppt.png");
} else if (type === 'txt' || type === 'TXT') {
return require("../../../assets/file/txt.png");
} else if (type === 'zip' || type === 'ZIP' || type === 'rar') {
return require("../../../assets/file/zip.png");
}
return require("../../../assets/file/unknown.png");

},

filepreview(url) {
openFile(url);




},
hidePreview() {
this.showFileList = false;
this.listFile = [];
},
onNavTabIndexChange(val) {
if (this.navTabIndex !== val) {
this.navTabIndex = val;
}
},
onRightShowChange(val) {
if (this.rightShow !== val) {
this.rightShow = val;
}
},
onLeftIndexChange(val) {
if (this.leftIndex !== val) {
this.leftIndex = val;
}
},
onSceneIndexChange(val) {},
onScenePostInfoChange(val) {},
onScrollTopChange(val) { //主筛选右列表滚动
if (this.filtrateScrollTop !== val) {
this.filtrateScrollTop = val;
}
},
onFiltrateSure(data) { //主筛选点击确定
console.log(data);
for (var key in data.left) {
data.right[key] = data.left[key];
}
this.filtratePostParams = data.right;
this.$refs.my_scroller.triggerPullToRefresh();
},

handleSelectedInfo(data) { //筛选跳转负责人返回的数据
var field = data.section;
var filtrateData = this.logListInfo.filtrateData;
for (var i = 0; i < filtrateData.length; i++) {
var filtrateItem = filtrateData[i];
if (filtrateItem.field === field) {
filtrateItem.content = data.item.user_name;
filtrateItem.fieldvalue = data.item.role_id;
}
}
console.log(filtrateData);
this.logListInfo.filtrateData = filtrateData;
this.$store.commit('updateLogListInfo', this.logListInfo);
},
}
}




