import { AjaxHttp } from '../../../js/ajaxHttp'

const EVENT_REPLY = 'reply'
const EVENT_CLICK = 'click'
const EVENT_PREVIEW = 'preview'

export default {
  name: 'loglist-cell',
  components: {},
  data() {
    return {
      praiseImage: require("../../../assets/common_praise_light_normal.png"),
    }
  },
  props: {
    data: {
      type: Object,
      default: {
        user_name: '',
        create_date: '',
        content: '',
        img: '',
        comment_count: '',
        praise_count: '',
        is_praised: false,
        id: '',
        file_list: [],
        show_file: {
          type: Object,
          default: {
            img: '',
            title: '',
            detail: '',
            show: false
          }
        }
      }
    }
  },
  computed: {},
  mounted() {
    this.getPraiseImage();
  },
  methods: {
    praise() {
      AjaxHttp('POST', 'm=log&a=praise ', { id: this.data.id }, this, function(res, wself, success) {
        if (success) {
          wself.data.is_praised = res.data.is_praise;
          wself.$set(wself.data, 'praise_count', res.data.number === '0' ? '赞' : res.data.number);
          wself.getPraiseImage();
        }
      });
    },
    reply() {
      this.$router.push({
        name: 'createcomment',
        params: {
          type: 'defalut',
          id: this.data.id,
          receiveid: '0',
          name: this.data.user_name,
        }
      })
    },
    click() {
      this.$emit(EVENT_CLICK, this.data);
    },
    preview() {
      this.$emit(EVENT_PREVIEW, this.data);
    },
    getPraiseImage() {
      if (this.data.is_praised === 1) {
        this.praiseImage = require("../../../assets/common_praise_light_selected.png");
      } else {
        this.praiseImage = require("../../../assets/common_praise_light_normal.png");
      }
    }
  }
}




